// md

@media (max-width: 1059px) {

	#md-indicator {
		display: block;
	}

	.container {
		max-width: 100% !important;
		padding: 0 32px;
	}

	.navbar-wrapper {
		display: none;
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		//background: #00E6B3;
		background: #fff;
		overflow: auto;
		margin: 0;
		z-index: 400;
	}

	.navbar-trigger {
		width: 32px;
		height: 32px;
		position: absolute;
		right: 32px;
		top: 25px;
		display: block;
		cursor: pointer;
		z-index: 500;
	}


	.navbar-trigger:before {
		content: "";
		background: url(../images/navbar-trigger.svg) 0 0 no-repeat;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		transition: all .2s ease;
	}


	.navbar-trigger:after {
		content: "";
		background: url(../images/navbar-trigger-active.svg) 0 0 no-repeat;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		transition: all .2s ease;
		opacity: 0;
	}

	.navbar-trigger.active:before {
		opacity: 0;
	}

	.navbar-trigger.active:after {
		opacity: 1;
	}

	.header-content {
		padding: 0 60px 0 0;
	}

	.navbar-wrapper .header-logo {
		//display: block;
	}

	.navbar-wrapper-inner {
		min-height: 100%;
		padding: 28px 32px;
	}

	ul.navbar-nav {
		margin: 153px 0 0 30px;
		display: block;
		font-weight: bold;
		font-size: 56px;
		line-height: 64px;
	}

	ul.navbar-nav > li {
		margin: 0 0 48px 0;
	}

	.top-col-1 {
		order: 2;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.top-col-2 {
		order: 1;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.section-top-pic {
		text-align: center;
		margin: 0 0 8px 0;
	}

	main {
		padding: 121px 0 0 0;
	}

	h1,
	.h1 {
		font-size: 56px;
		line-height: 64px;
	}

	.section-top-descr {
		padding: 20px 32px 0 32px;
	}

	.section-top {
		margin: 0 0 75px 0;
	}

	.section-champ {
		padding-top: 126px;
		padding-bottom: 130px;
		border: 2px solid #142664;
	}

	.section-header {
		margin: 0 0 48px 0;
	}

	.champ-list {
		overflow: auto;
		margin: 0 -32px;
		padding: 0 32px;
	}

	.champ-list .row {
		flex-wrap: nowrap;
	}
	
	.champ-list .col {
		flex: 0 0 448px;
		max-width: 448px;
	}

	.champ-list .col-last {
		display: none;
	}

	.champ-info-mob {
		display: block;
		padding: 68px 0 0 29px;
	}

	.champ-info h3, .champ-info .h3 {
		margin: 0 0 24px 0;
	}

	.champ-info-mob p {
		max-width: 430px;
	}

	.section-stages {
		padding-top: 128px;
	}

	.stages-col-1 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.stages-col-2 {
		display: none;
	}

	.stages-list {
		margin-top: 64px;
	}

	.stage-tmb-date {
		margin: 0 31px 0 0;
	}

	.prizes-fund {
		font-size: 118px;
		line-height: 118px;
		margin: 27px 0 104px 0;
	}

	.prizes-wrapper {
		padding: 0 32px;
	}

	.prize-ind .cont {
		display: block;
		padding: 77px 78px 78px 78px;
	}

	.prize-ind-descr {
		display: block;
	}

	.prize-ind-pic {
		margin: 0 0 55px 0;
	}

	.prize-ind h3, .prize-ind .h3 {
		margin: 0 0 24px 0;
	}

	.prize-ind-num {
		text-align: left;
		margin: 36px 0 0 0;
	}

	.main-prize-col-1 {
		order: 2;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.main-prize-col-2 {
		order: 1;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.main-prize-pic {
		text-align: center;
	}

	.section-main-prize {
		margin-top: 151px;
	}

	.main-prize-descr {
		padding: 65px 32px 0 32px;
		max-width: 540px;
	}

	.section-partners {
		margin: 132px 0 0 0;
		padding-top: 126px;
		padding-left: 32px;
		padding-right: 32px;
		padding-bottom: 98px;
		border: 2px solid #142664;
	}

	.partners-list {
		margin-top: 64px;
	}

	.partners-list .col {
		flex: 0 0 50%;
		max-width: 50%;
	}

