@media (max-width: 767px) {

	.prizes-wrapper .anim,
	.prizes-fund,
	.stage-tmb,
	.champ-list .col,
	.anim,
	.section-top-pic {
		opacity: 1 !important;
		transform: none !important;
	}

}

.section-top .anim:not(img) {
	opacity: 0;
	transform: translateY(50px);
}

.section-top-pic {
	opacity: 0;
	transform: scale(.75);
}

.champ-list .col {
	opacity: 0;
	transform: translateY(50px) scale(.95);
}

.stage-tmb {
	opacity: 0;
	transform: translateY(50px);
}

.prizes-fund {
	opacity: 0;
	transform: translateY(50px);
}

.prizes-wrapper .anim {
	opacity: 0;
	transform: translateY(50px) scale(.95);
}